<template>
  <div class="login-container">
    <el-form ref="loginForm" :model="loginForm" :rules="loginRules" class="login-form" auto-complete="on" label-position="left">
        
        <div class="logo">
          <img style="background-size: 100% 100%;width: 100%;height: 100%;" src="@/assets/logo大.png" alt="">
        </div>
        <!-- <div class="title-container">
            <h3 class="title">GORGEOUS NBA 后台管理系统</h3>
        </div> -->

        <el-form-item prop="username">
            <div class="el-container">
                <span class="icon-container">
                    <i class="el-icon-user-solid"></i>
                </span>
                <el-input 
                    v-model="loginForm.username"
                    placeholder="请输入用户名"
                    name="username"
                    type="text"
                    tabindex="1"
                    auto-complete="on"
                ></el-input>
            </div>
        </el-form-item>

        <el-form-item prop="password">
            <div class="el-container">
                <span class="icon-container">
                    <i class="el-icon-lock"></i>
                </span>
                <el-input
                    key="password"
                    ref="password"
                    v-model="loginForm.password"
                    type="password"
                    placeholder="请输入密码"
                    name="password"
                    tabindex="2"
                    auto-complete="on"
                    @keyup.enter.native="handleLogin" 
                    show-password
                ></el-input>
            </div>
        </el-form-item>

        <el-button type="primary" style="width: 100%;margin-bottom:30px;" @click.native.prevent="handleLogin('loginForm')">登录</el-button>

        <div class="tips">
            <span style="margin-right:20px;">后台管理系统</span>
            <span> 需要管理员身份用户</span>
            <h5>管理员测试账号（用户名：guoqian123  密码：abc123）</h5>
        </div>
    </el-form>
  </div>
</template>

<script>
import { mapMutations } from 'vuex'
export default {
    data(){
        const validateUsername = (rule, value, callback) => {
            if (value === '') {
                callback(new Error('Please enter the correct user name'))
            } else {
                callback()
            }
        }
        const validatePassword = (rule, value, callback) => {
            if (value.length < 6) {
                callback(new Error('The password can not be less than 6 digits'))
            } else {
                callback()
            }
        }
        return{
            loginForm:{
                username:'',
                password:''
            },
            loginRules: {
                username: [
                    { required: true, message: '用户名不能为空',trigger: 'blur'},
                    {
                        pattern: /^(?!(\d+)$)[a-zA-Z\d_]{4,20}$/,
                        message: "账号长度4-20，必须包括数字、字母，可包括下划线",
                        trigger: "blur"
                    },
                    {validator: validateUsername }
                ],
                password: [
                    { required: true,message:'密码不能为空', trigger: 'blur'},
                    {
                        pattern: /^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{6,20}$/,
                        message: "密码长度为6-20位，必须且只能包括数字、字母",
                        trigger: "blur"
                    },
                    {validator: validatePassword }
                ]
            },
            loading: false,
            redirect: undefined
        }
    },
    methods:{
        ...mapMutations(['updateToken']),
        handleLogin(loginForm){
            this.$refs[loginForm].validate((valid)=>{
                if(valid){
                    this.loading = true
                    this.$axios.post('/api/backlogin',this.loginForm).then(res=>{
                        if(res.status == 0){
                            this.$message({
                                type:'success',
                                message:'用户登录成功'
                            })
                            this.updateToken(res.token)
                            localStorage.setItem('token',res.token)
                            const token = localStorage.getItem('token')
                            this.$store.commit('updateToken',token)
                            this.$store.dispatch('getUserInfoActions')
                            this.$router.push({path: this.redirect || '/'})
                        }else if(res.status == 1){
                            this.$message.error({
                                message:'登陆失败！请检查用户名或密码是否正确！'
                            })
                        }else{
                            this.$message({
                                type: 'warning',
                                message:'未知错误！'
                            })
                        }
                    })
                }else{
                    this.$message({
                        type: 'warning',
                        message:'用户登录失败，请重试！'
                    })
                    console.log('error submit !!!');
                    return false
                }
            })
        }
    }
}
</script>

<style scoped>
.login-container{
    min-height: 100%;
    width: 100%;
    background: url('@/assets/中草药滚动背景.png');
    background-repeat: no-repeat;
    background-size: cover;
    background-size: 140% 100%;
    animation: gradientBG 30s ease infinite;
    width: 100%;
    height: 100%;
    min-height: 100vh;
    flex-direction: column;
    -webkit-display:flex;
    display:flex;
    overflow: hidden;
}
@keyframes gradientBG {
  0% {
      background-position: 0% 50%;
  }
  50% {
      background-position: 100% 50%;
  }
  100% {
      background-position: 0% 50%;
  }
}
.title-container{
    color: #fff;
    font-size: 20px;
    text-align: center;
}
.logo{
  background-image: linear-gradient(to right, rgb(89, 89, 89),rgb(255, 255, 255));
  -webkit-background-clip: text;
  color: transparent;
  width: 170px;
  height: 90px;
  margin: 0 auto;
  font-size: 30px;
  font-weight: 600;
  letter-spacing: -1px;
  transition: all 0.3s linear;
  text-align: center;
}
.title-container h3{
    margin: 35px 0;
}
.login-form {
    position: relative;
    width: 520px;
    max-width: 100%;
    padding: 20px 35px 20px 35px;
    margin: 160px auto;
    overflow: hidden;
    box-sizing: border-box;
    /* background: rgba(255, 255, 255, 0.3); */
    border-radius: 10px;
}
.el-form-item{
    border: 1px solid rgba(255, 255, 255, 0.1);
    background: rgba(0, 0, 0, 0.1);
    border-radius: 5px;
    color: #454545;
    margin: 26px 0;
}
.el-container{
    display: flex;
    border-radius: 5px;
}
.icon-container{
    padding: 0 10px;
    color: #fff;
    font-size: 20px;
}
.el-input /deep/ .el-input__inner{
    border: none;
    background: #0000;
    color: #fff;
}
.tips{
    color: rgb(165, 165, 165);
    font-size: 12px;
}
</style>